.profile {
  display: flex;
  max-width: 880px;
  align-items: center;
  justify-content: space-between;
  padding: 40px 0 51px;
  margin: auto;
}

@media screen and (max-width: 655px) {
  .profile {
    flex-direction: column;
    align-items: center;
    padding: 40px 0 35px;
  }
}
